@import url('https://fonts.googleapis.com/css2?family=League+Spartan&family=Nunito:wght@200&family=Orbitron&family=Plus+Jakarta+Sans:wght@200&family=Roboto:ital,wght@0,700;1,300&display=swap');


* {
    margin: 0; 
    padding: 0; 
}

.card-container {
    display: flex; 
    justify-content: center;
    align-items: center; 
}

.card-row {
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid #5D576B;
    width: 900px;
}
.card-row>.r {
    flex:content;
}
.name {
    color: #ED6A5A;  
}
.cs {
    font-weight: bold; 
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: orange; 
}
.college {
    font-weight: normal;
}
.hover-3 {
    font-size: 19.5px;
    font-weight: bold;
    font-family: 'League Spartan', sans-serif;
 
    /* the color  */
    --c: #1095c1;
    /* the height */
    --h: 1.2em;
  
    /* The first hover effect in this article */
    line-height: var(--h);  
    color: #0000;
    overflow: hidden;
    text-shadow: 
      0 calc(-1 * var(--_t, 0em)) var(--c), 
      0 calc(var(--h) - var(--_t, 0em)) #E6EBE0;
    /* The second hover effect from the previous article */
    background: 
      linear-gradient(var(--c) 0 0) no-repeat 
      calc(200% - var(--_p, 0%)) 100% / 200% var(--_p, .08em);
    transition: .3s var(--_s, 0s), background-position .3s calc(.3s - var(--_s, 0s));
  }
  .hover-3:hover {
    --_t: var(--h);
    --_p: 100%;
    --_s: .3s
  }
.major {
    font-weight: bold; 
    color: rgb(11, 108, 11); 
}
.college {
    color: #5D576B; 
}
.name, .college, .major {
    margin-right: 10px; 
}
a {
    text-decoration: none; 
    color: none; 
}