* {
  margin: 0;
  padding: 0;
}

.App {
  min-height: 100vh; 
  min-width: 100vw;
  background-color: #E6EBE0; 
}

.hover-1 {
  font-size: 40px; 
  margin: 6px;  
  line-height: 1.2em;
  color: #0000;
  text-shadow: 
    0 0 #5D576B, 
    0 1.2em #F7C59F;
  overflow: hidden;
  transition: .3s;
}
.hover-1:hover {
  text-shadow: 
    0 -1.2em #5D576B, 
    0 0 #F7C59F;
}
.mail {
  color: rgb(11, 108, 11); ; 
  transition: color 1s; 
}

.mail:hover {
  color: #5D576B; 
}
.top {
  display: flex; 
  flex-direction: column;
  justify-content: center; 
  align-items: center;
}


.input {
  height: 32px; 
  width: 540px; 
}

.count {
  margin-left: 6px; 
  color: rgb(11, 108, 11); 
}
footer p {
  color: #ED6A5A; 
  margin: 10px; 
}

.input {
  border-radius: 12px;
  color: #ED6A5A;
  border-style: solid;
  border-color: #5D576B;
  border-width: 1px;
}
footer {
  display: flex; 
  justify-content: center; 
  
}
nav img {
  cursor: pointer; 
  margin: 3px; 
  height: 64px; 
  width: auto; 
}

nav {
  display: flex;
  justify-content: center; 
}